@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mitr:wght@600&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Mitr", "Varela Round", "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  padding: 20px;

}

#root {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;

}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

@media (max-width: 414px) {
  body {
   
    padding: 0px;
  
  }
}
.App {
  width: 1300px;
  min-height: 500px;
  margin-top: 20px;
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.288);
  border-radius: 50px;
}

@media (max-width: 360px) {
  .App {
    margin-top: 0px;
    width: 100vw;
    border-radius: 0px;
  }
}

.Start {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 570px;
  background-color: steelblue;
  border-radius: 10px;
  z-index: 5;

}
.pokelogo {
    width: 40%;
    height: 40%;
    margin-top: 30px;
}

.Start h1 {
    font-size: 40px;
    margin-top: -20px;
    margin-bottom: 60px;
    color: rgb(235, 235, 1);
    text-shadow: 0px 3px #3b45ca;
}

.Start p {
    margin-bottom: 20px;
    color: rgb(255, 255, 255);
}

.btn_start {
    width: 200px;
    background-color: hsl(120, 80%, 53%);
}

@media (max-width: 360px) {
    .pokelogo {
        width: 50%;
        height: 15%;
        margin-top: 100px;

    }

    .Start {
        height: 100vh;
    }

    .Start h1 {
        font-size: 30px;
         margin-top: -10px;
    margin-bottom: 100px;

    }

    .Start p {
        font-size: 16px;
       margin-left: 10px;
    }
}


.Status {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 570px;
  border-radius: 10px;
  z-index: 7;
}

.game_over {
  background-color: rgb(250, 46, 46);
    
}

.perfect {
  background-color: rgb(146, 199, 23);
}

.Status h1 {
  font-size: 100px;
  margin-top: -20px;
  margin-bottom: 60px;
  color: rgb(235, 235, 1);
  text-shadow: 0px 3px #3b45ca;
  margin-top: 150px;
}

.Status p {
  margin-bottom: 10px;
  color: rgb(255, 255, 255);
}

.your_score {
  font-size: 50px;
}

.btn_status {
  width: 200px;
  background-color: hsl(234, 100%, 65%);
}

.PokemonGrid {
  display: flex;
  flex-flow: column;
  height: 570px;
  background-color: rgb(219, 219, 219);
  border-radius: 50px;
}

.score_container {
  display: flex;
  flex-flow: column;
  align-items: center;
  background-color: rebeccapurple;
  padding: 10px;
  border-radius: 50px 50px 0 0;
}

.current_score_container {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border-radius: 20px;
  color: #ffff00;
}

.current_score_container > p {
  font-size: 24px;
}

.yourscore {
  margin-left: 10px;
  font-size: 48px;
}

.pkmngrid {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: auto;
  justify-items: center;
  grid-gap: 5px;
  align-items: center;
  width: 100%;
  height: 570px;
  background-color: rgb(219, 219, 219);
  border-radius: 10px;
}

@media (max-width: 360px) {
  .PokemonGrid {
    height: 100vh;
  }
  .sco re_container {
    display: flex;
    flex-flow: column;
    align-items: center;
    background-color: rebeccapurple;
    padding: 10px;
    border-radius: 0px 0px 0 0;
  }

  .pkmngrid {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2px;
    border-radius: 0px;
  }
}

.Pokemon {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin: 5px;
  border: 5px solid #3b45ca;
  border-radius: 20px;
  width: 180px;
  height: 180px;
  background-color: rgba(233, 233, 31, 0.829);
  box-shadow: 0 3px 10px 1px rgba(0, 0, 0, 0.418);
}

.pokepic {
  width: 70%;
  height: 70%;
}

.Pokemon:active {
  transform: scale(0.98);
}

.Pokemon > div {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

@media (max-width: 360px) {
  .Pokemon {
    width: 90px;
    height: 110px;
  }

  .pokepic {
    width: 80%;
    height: 80%;
  }

  .pokeinfo {
    font-size: 12px;
  }
}

